import React from 'react';
import '../assets/css/Datainput.css';
import { NavLink } from 'react-router-dom';
import Goback from './Goback';

function Datainput() {

    return (
        <div className="datainput">

            <h3>Carga de datos</h3>
            <div className="row options">

                <div className="col-6">
                    <NavLink to="/departments">
                        <div className="input-button">
                        <h6><i class="bi bi-building icon"></i> Departamentos</h6>
                        </div>
                    </NavLink>
                </div>

                <div className="col-6">
                    <NavLink to="/services">
                        <div className="input-button">
                            <h6><i class="bi bi-door-open"></i> Servicios</h6>
                        </div>
                    </NavLink>
                </div>

            </div>
            <div className="row options">
                <div className="col-6">
                    <NavLink to="/guard">
                        <div className="input-button">
                            <h6><i class="bi bi-shield-plus"></i> Guardias, Turnos...</h6>
                        </div>
                    </NavLink>
                </div>
                <div className="col-6">
                    <NavLink to="/personal">
                        <div className="input-button">
                            <h6><i class="bi bi-person"></i> Personal</h6>
                        </div>
                    </NavLink>
                </div>
            </div>
            <div className="row options">
                <div className="col-6">
                    <NavLink to="/licences">
                        <div className="input-button">
                            <h6><i class="bi bi-file-earmark-medical"></i> Licencias</h6>
                        </div>
                    </NavLink>
                </div>
                <div className="col-6">
                    <NavLink to="/notworking">
                        <div className="input-button">
                            <h6><i class="bi bi-calendar2-minus"></i> Feriados</h6>
                        </div>
                    </NavLink>
                </div>
            </div>
            <Goback />
        </div>


    )
}

export default Datainput;