import React, { useState, useEffect } from 'react';
import DepartmentForm from './DepartmentForm';
import Goback from './Goback';
import useToggle from '../../UseToggle'
import '../../assets/css/datainputs/Departments.css';
import instance from '../axios';
import Inputoptions from './Inputoptions';

function Departments() {

    const [show, setShow] = useToggle();
    const [deplist, setdeplist] = useState([]);
    const [depName, setDepName] = useState();
    const [depId, setDepId] = useState();

    function after(response){
        setShow()
        updateList();
        alert(response);
    }

    /* --------------- UPDATE LIST --------------*/ 

    function updateList(){
        instance.get('/department/')
            .then(res => {
                console.log(res.data)
                setdeplist(res.data)
            })
            .catch(Error)
    }

    useEffect(() => {
        updateList()
    }, [])

    /* ------------ UPDATE ELEMENT ID -------------*/

    function addValue(id, name) {
        setDepName(name);
        setDepId(id);
        if (!show){
            setShow()
        }
        
    }

    /*-------------- DELETE ELEMENT-------------------*/
       

    function deleteElement(e){
        instance.delete('/department/' + e)
            .then((response) => { after(response.data) });
    }

    /*-------------------RESET VALUES------------------*/

    function resetValues(){
        setShow();
        setDepName();
        setDepId();
    }

    /* ------------ DEPARTMENT COMPONENT -------------*/ 

    return (
        <div className="departments">
            <Goback />
            <Inputoptions />
            {show ? <DepartmentForm name={depName} id={depId} after={after} /> : null}
            <button className="btn btn-outline-success new-entry" onClick={() => resetValues()}>{show ? <div><i class="bi bi-x-lg"></i><p>Cerrar</p></div> : <div><i class="bi bi-plus-lg"></i> <p>Nuevo</p></div>}</button>
            <br />
            <table className="table">
                <thead>
                    <tr>
                        <th className="th-name" scope="col">Departamento</th>
                        <th className="th-option" scope="col">Opciones</th>
                    </tr>
                </thead>
                <tbody>
                    {deplist.map(dep => (
                        <tr key={dep.id}>
                            <td className="td-name" name={dep.id} value={dep.name}>{dep.name}</td>
                            <td className="td-option">
                                <button className="btn btn-outline-success"
                                    id={dep.id}
                                    name={dep.name}
                                    onClick={(e) => addValue(e.currentTarget.id, e.currentTarget.name)}>
                                    <i class="bi bi-pencil-square"></i></button>
                                <button className="btn btn-outline-success"
                                    value={dep.id}
                                    id={dep.id}
                                    onClick={(e) => { if (window.confirm('¿Confirma eliminar?')) { deleteElement(e.currentTarget.id) } }}>
                                    <i class="bi bi-trash"></i></button></td>
                        </tr>
                    ))}

                </tbody>
            </table>


        </div>
    )
}

export default Departments;